.HeaderItem {
  display: inline-block;
  line-height: 60px;
  color: #fff;
  text-decoration: none;
  padding: 0 10px;
  min-width: 80px;
  text-align: center;
  position: relative;
}

.HeaderItem.ItemActive::after {
  content: "";
  position: absolute;
  border-bottom: 3px solid #fff;
  bottom: 0;
  left: 0;
  width: 100%;
}
