.Header {
  background-color: #5473e8;
  height: 60px;
  /* box-shadow: 0px 0px 3px 0px rgb(0 0 0 / 40%); */
}

.HeaderContainer {
  display: flex;
}

.HeaderContainer > .HeaderMenuItem:first-child {
  min-width: 170px;
}

@media only screen and (max-width: 600px) {
  .HeaderContainer {
    justify-content: center;
  }

  .HeaderContainer.SearchFocus > .HeaderMenuItem {
    display: none;
  }

  .HeaderContainer:not(.SearchFocus) > .HeaderSearch {
    --color: #fff;
    --bg: none;
    flex: 0;
    cursor: pointer;
  }

  .HeaderContainer:not(.SearchFocus) > .HeaderSearch > input {
    display: none;
    width: 0;
  }
}

.HeaderSearch {
  --color: #fff;
  --bg: #4460cb;
  display: flex;
  flex: 1;
  margin: 0 10px;
  background-color: var(--bg);
  padding: 0 20px;
  align-items: center;
  cursor: text;
  height: 60px;
  transition: flex 200ms linear, width 200ms linear;
}

.HeaderSearch svg {
  color: var(--color);
}

.HeaderSearch input {
  flex: 1;
  padding: 0;
  margin: 0;
  border: 0;
  outline: 0;
  background-color: transparent;
  margin-left: 10px;
  color: var(--color);
  text-overflow: ellipsis;
  display: inline-block;
  transition: width 200ms linear;
}

.HeaderSearch input::placeholder {
  color: var(--color);
}

.SearchFocus > .HeaderSearch {
  --color: #000;
  --bg: #fff;
}
